.header .navegacion {
  display: flex;
  justify-content: space-between;
}
.header .navegacion .logo {
  color: #008080;
  font-size: 3rem;
  font-weight: bold;
  padding: 0 4rem;
  line-height: 8rem;
}
.header .navegacion_menu {
  display: flex;
  margin-right: 4rem;
  list-style: none;
}
.header .navegacion_li {
  text-decoration: none;
  font-size: 1.8rem;
  margin: 0 1rem;
  line-height: 4rem;
  width: max-content;
}
.header .navegacion__enlace-active {
  color: #29F48F;
  background-color: #008080;
  transition: 0.5s;
}
.header .navegacion__enlace {
  color: #29F48F;
  padding: 0.8rem 1.2rem;
  border-radius: 2rem;
}
.header .navegacion__enlace:hover {
  color: #29F48F;
  background-color: #008080;
  transition: 0.5s;
}
.header .nav-toggle {
  color: white;
  background-color: #3c0446;
  border: none;
  font-size: 4rem;
  padding: 0 2rem;
  line-height: 6rem;
  display: none;
  cursor: pointer;
  height: 6rem;
}
@media (max-width: 768px) {
  .header .header__barra {
    height: 6rem;
  }
  .header .logo {
    font-size: 2.5rem;
    font-weight: bold;
    padding: 0 2rem;
    line-height: 6rem;
  }
  .header .navegacion_menu {
    flex-direction: column;
    align-items: center;
    margin-right: 4rem;
    position: fixed;
    left: 0;
    top: 4rem;
    width: 100%;
    padding: 2rem 0;
    height: calc(100% - 6rem);
    overflow-y: auto;
    left: 100%;
    transition: left 0.3s;
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#100010+0,3c2846+66,ffffff+100&0.8+1,0.5+100 */
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#100010+0,3c2846+66,4e008e+100&0.8+1,0.8+100 */
    background: -moz-linear-gradient(-45deg, rgba(16, 0, 16, 0.8) 0%, rgba(17, 1, 17, 0.8) 1%, rgba(60, 40, 70, 0.8) 66%, rgba(78, 0, 142, 0.8) 100%);
    /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg, rgba(16, 0, 16, 0.8) 0%, rgba(17, 1, 17, 0.8) 1%, rgba(60, 40, 70, 0.8) 66%, rgba(78, 0, 142, 0.8) 100%);
    /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg, rgba(16, 0, 16, 0.8) 0%, rgba(17, 1, 17, 0.8) 1%, rgba(60, 40, 70, 0.8) 66%, rgba(78, 0, 142, 0.8) 100%);
    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc100010", endColorstr="#cc4e008e",GradientType=1 );
    /* IE6-9 fallback on horizontal gradient */
  }
  .header .navegacion__enlace-active {
    background: none;
  }
  .header .navegacion__enlace {
    font-size: 2rem;
  }
  .header .navegacion__enlace:hover {
    background: none;
    color: #008080;
  }
  .header .nav-toggle {
    display: block;
  }
  .header .nav-menu__visible {
    left: 0;
  }
}
.header .header__contenido {
  padding: 5rem 0;
  text-align: center;
}
.header .header__contenido .header__texto {
  margin-bottom: 2rem;
}
.header .header__contenido .header__texto span {
  font-size: 3rem;
  color: #ffffff;
}
.header .header__contenido .header__texto h1 {
  color: #008080;
  font-size: 4rem;
  margin: 1rem;
}
.header .header__contenido .header__texto h1 span {
  color: #29F48F;
  font-size: 6rem;
}
.header .header__contenido .header__texto h1:hover {
  color: #29F48F;
}
.header .header__contenido .header__img2 {
  width: 300px;
}
@media (min-width: 768px) {
  .header .header__contenido {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 10rem 0;
    align-items: center;
  }
  .header .header__contenido .header__texto span {
    font-size: 4rem;
    color: #ffffff;
  }
  .header .header__contenido .header__texto h1 {
    color: #008080;
    font-size: 6rem;
    margin: 1rem;
  }
  .header .header__contenido .header__texto h1:hover {
    color: #29F48F;
  }
  .header .header__contenido .header__img2 {
    width: 500px;
  }
}
.header .fijo {
  position: fixed;
  width: 100%;
