  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(16, 0, 16, 0.8) 0%, rgba(17, 1, 17, 0.8) 1%, rgba(60, 40, 70, 0.8) 66%, rgba(78, 0, 142, 0.8) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(16, 0, 16, 0.8) 0%, rgba(17, 1, 17, 0.8) 1%, rgba(60, 40, 70, 0.8) 66%, rgba(78, 0, 142, 0.8) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc100010", endColorstr="#cc4e008e",GradientType=1 );
