.about .about__titulo {
  font-size: 4rem;
  line-height: 3rem;
  text-align: center;
  color: #008080;
  margin-bottom: 2.5rem;
}
.about .about__grid {
  text-align: center;
}
@media (min-width: 768px) {
  .about .about__titulo {
    margin-top: 5rem;
    text-align: left;
    align-items: center;
  }
  .about .about__grid {
    text-align: left;
    display: grid;
    gap: 2.5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.about .about__contenido--texto p {
  font-weight: 400;
  line-height: 3rem;
}
.about .about__contenido--texto button {
  font-size: 2rem;
  background-color: #008080;
  font-weight: 900;
  color: #29F48F;
  padding: 1.5rem 8rem 1.5rem 8rem;
  border-radius: 1rem;
  border: none;
}
.about .about__contenido--texto button:hover {
  background-color: #29F48F;
  color: #008080;
  cursor: pointer;
}
@media (min-width: 768px) {
  .about .about__contenido--texto button {
    padding: 1.5rem;
  }
}
@media (min-width: 768px) {
  .about .about__skill_titulo {
    margin-top: 2rem;
  }
}
.about .about__skills__iconos {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(4, 1fr);
}
.about .about__skills__iconos p {
  margin: 0 0 1rem 0;
}
.about .about__skills__iconos .about__skill__icono {
  border: 0 solid #e9d2e3;
  border-width: 1px;
  display: block;
  text-align: center;
}
.about .about__skills__iconos .about__skills__img {
  margin: 1rem;
  width: 35px;
}
@media (min-width: 768px) {
  .about .about__skills__iconos {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(5, 1fr);
  }
}

